export interface CardView {
    points: number;
    type: number;
}

export class BoardView {
    private _cardTypes = ["â¤ï¸", "âš”ï¸", "ðŸ›¡ï¸"];

    public setCardClickHandler(handler: EventListenerObject): void {
        let cards = document.querySelectorAll('#player-cards .card');
        cards.forEach( (element): void => {
            element.addEventListener('click', handler);
        });
    }

    public renderPlayerCards(cards: CardView[]): void {
        this._renderCards(cards, this._getPlayerCards());
    }

    public renderOponentCards(cards: CardView[]): void {
        this._renderCards(cards, this._getOponentCards());
    }

    private _renderCards(cards: CardView[], nodes: NodeListOf<Element>): void {
        for (let i = 0; i < cards.length; i++){
            nodes[i].innerHTML = this._cardTemplate(
                i,
                cards[i].points,
                cards[i].type
            );
        }
    }

    private _getPlayerCards(): NodeListOf<Element> {
        return this._getCards('player-cards');
    }

    private _getOponentCards(): NodeListOf<Element> {
        return this._getCards('oponent-cards');
    }

    private _getCards(containerID: string): NodeListOf<Element> {
        return document.querySelectorAll(`#${containerID} .card`);
    }

    private _cardTemplate(
        cardindex: number,
        points: number,
        type: number
    ): string {
        let template = "";
        const cardLabel = points.toString();
        const cardSuit = this._cardTypes[type];
        template = `
            <div id="${cardindex}" class="card-value-top">${cardLabel}</div>
            <div id="${cardindex}" class="card-suit">${cardSuit}</div>
            <div id="${cardindex}" class="card-value-bottom">${cardLabel}</div>
        `
        return template;
    }
}
